<template>
  <div class="mark-down">
    <MarkdownPreview v-if="markdown" :initialValue="markdown" />
  </div>
</template>

<script>
import { MarkdownPreview } from "vue-meditor";
export default {
  components: {
    MarkdownPreview
  },
  data() {
    return {
      markdown: null
    };
  },
  watch: {
    "$route.params": {
      handler({ type, url }) {
        let value;
        if (type) {
          switch (type) {
            case "@/share":
              value = require(`@/share/${url}`);
              break;
          }
        }
        this.markdown = value;
      },
      immediate: true
    }
  },
  mounted() {
  },
  beforeDestroy() {
    this.markdown = null;
  }
};
</script>

